
  function sround(val) {
    return String(Math.round(val*10000)/10000)
   }

   import {getGradientColor} from './helpers';

export function gtime1(time, green = 60, red= 60) { //green
  time = time % (red + green);
  // red = red - 2
  // green = green - 2
  
  // let greenr = green + 4
  let redr = red + 4

  return   time <= redr ? 0 : time - redr
}

export function gtime2(time, green = 60, red= 60) { //red
time = time % (red + green);
// red = red - 2
// green = green - 2

// let greenr = green + 4
let redr = red + 4

return time <= redr ? time : redr;
}

export function gtime3(time, green = 60, red= 60) { //green
time = time % (red + green);
// red = red - 2
// green = green - 2

// let greenr = green + 4
let redr = red + 4

return  time <= redr ? green : green - (time-redr)
}

export function gtime4(time, green = 60, red= 60) { //red
time = time % (red + green);

// red = red - 2
// green = green - 2

// let greenr = green + 4
let redr = red + 4

return  time <= redr ? redr-time : 0;
  }



  export function rtime1(time, green = 60, red= 60) { //red
    time = time % (red + green);
    // red = red - 2
    // green = green - 2

    
    // let greenr = green + 4
    // let redr = red + 4

    return   time <= 2 ?  green + 2 + time  
          : time <= (red+2)  ? 0
          : time - (red+2);
  }

  export  function rtime2(time, green = 60, red= 60) { //green
    time = time % (red + green);
    // red = red - 2
    // green = green - 2

    
    // let greenr = green + 4
    // let redr = red + 4
    
    return  time <= 2 ? red :
          time <= (red+2) ? time-2 : 
          red;
  }

export function rtime3(time, green = 60, red= 60) { //red
    time = time % (red + green);
    // red = red - 2
    // green = green - 2

    
    let greenr = green + 4
    let redr = red + 4

    return  time <= 2 ?  2 - time  :
          time <= (red+2)  ? greenr : 
          redr - (time - (green + 2));
}

export  function rtime4(time, green = 60, red= 60) { //green
    time = time % (red + green);
    // red = red - 2
    // green = green - 2

    
    // let greenr = green + 4
    // let redr = red + 4

    return  time <= 2 ?  0  :
          time <= (red+2)  ? red + 2 - time : 0;
  }

export class Sign {
  constructor(group, position, isGreen, show = false, name = '') {
    this.group_id = group
    this.position = position

    this.isGreen = isGreen
    this.show = show
    this.name = name
  }


  get position_text() {
    return "[" + sround(this.position[0]) + "," + sround(this.position[1]) + "]";
  }


 

}



export class SignGroup {
  constructor(id, group,  time, time_red,time_green, connections, signs) {
    this.id = id
    this.group = group
    this.time = time
    this.signs = signs

    this.time_red = time_red
    this.time_green = time_green


    this.is_genetic = true;


    this.connection = connections
    this.sign_marker = undefined;
    this.time2 = 0;
    this.time_red2 = 0
    this.time_green2 = 0

    //statistics
    this.cars = 0;
    this.waiting_cars = 0;
    this.total_speed = 0;

    //statistics2
    this.cars2 = 0;
    this.waiting_cars2 = 0;
    this.total_speed2 = 0;
    this.time2 = 0;

    this.avg_speed_color = '#FF0000';
    this.set_speed_color();

    this.avg_speed_color2 = '#FF0000';
    this.set_speed_color2();


    this.is_lighting = false;
    
  }

  set_speed_color() {
    let av1 =   Math.round((this.cars > 0 ? this.total_speed/this.cars : 0) * 100)/100;
    //   // return "#c53900";
    
    this.avg_speed_color =     
    getGradientColor("#FF0000", "#00FF00", 
     Math.min( Math.max( av1 / 50, 0), 1.0 ));
  }

  
  set_speed_color2() {
    let av1 =   Math.round((this.cars2 > 0 ? this.total_speed2/this.cars2 : 0) * 100)/100;
    //   // return "#c53900";
    
    this.avg_speed_color2 =     
    getGradientColor("#FF0000", "#00FF00", 
     Math.min( Math.max( av1 / 50, 0), 1.0 ));
  }


  get name() {
    return this.sign_marker ?
    this.sign_marker.name
    : ""

  }

  get max_time() {
    return this.time_red + this.time_green + 3;
  }



  get position() {
    return  this.sign_marker ?
    " [" + this.sign_marker.position + "]"
    : ""

  }

  get offset() {
    return this.time2 > 0 ? this.time + " (" + this.time2 +  "²)" : "" + this.time ;
  }

  get avg_cars() {
    let av1 =  Math.round(this.cars/600*100)/100;
    // let av2 =  Math.round(this.cars2/600*100)/100;
    return "" + av1;
    // this.time2 > 0 ? av1 + " (" + av2 +  "²)" : 
    // "" +
    //  av1;
  }

  
  get avg_cars2() {
    // let av1 =  Math.round(this.cars/600*100)/100;
    let av2 =  Math.round(this.cars2/600*100)/100;
    return "" + av2;
    // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
  }

  get avg_waiting() {
    let av1 =  Math.round(this.waiting_cars/600*100)/100;
    // let av2 =  Math.round(this.waiting_cars2/600*100)/100
    // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
    return "" + av1;
  }

  
  get avg_waiting2() {
    // let av1 =  Math.round(this.waiting_cars/600*100)/100;
    let av2 =  Math.round(this.waiting_cars2/600*100)/100
    // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
    return "" + av2;
  }


  get avg_speed() {
    let av1 =   Math.round((this.cars > 0 ? this.total_speed/this.cars : 0) * 100)/100;
    // let av2 =   Math.round((this.cars2 > 0 ? this.total_speed2/this.cars2 : 0) * 100)/100;
    return "" + av1;
    // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
  }

  
  get avg_speed2() {
    // let av1 =   Math.round((this.cars > 0 ? this.total_speed/this.cars : 0) * 100)/100;
    let av2 =   Math.round((this.cars2 > 0 ? this.total_speed2/this.cars2 : 0) * 100)/100;
    return "" + av2;
    // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
  }


  // get avg_speed_color() {
  //    let av1 =   Math.round((this.cars > 0 ? this.total_speed/this.cars : 0) * 100)/100;
  //   // return "#c53900";
  //   return getGradientColor("#FF0000", "#00FF00", 
  //   Math.min( Math.max( av1 / 100, 0), 1.0 ));
    
    
  //   // return '#0000FF';
  //   // let av1 =   Math.round((this.cars > 0 ? this.total_speed/this.cars : 0) * 100)/100;
  //   // let av2 =   Math.round((this.cars2 > 0 ? this.total_speed2/this.cars2 : 0) * 100)/100;
  //   // return this.time2 > 0 ? av1 + " (" + av2 +  "²)" : "" + av1;
  // }

}

