<template>
  <div class="list-traffic">
      <el-button class="calculation" type="warning" plain @click="genetic_show"><i class="fas fa-calculator"></i> Find best setting</el-button>
      
      <el-popover placement="top-start" title="" width="200" trigger="hover" content="Load best solution found so far">
        <el-button slot="reference" class="calculation" type="success" plain @click="traffic_load(ServerBus.best_solution)">
          <i class="fas fa-upload"></i> Load Best Solution ({{Math.round(ServerBus.best_solution_result)}} s)</el-button>
      </el-popover>

    <MessageBox title="Parameters of a genetic algorithm" ok="Run Genetic Algorithm" v-if="isShowGenetic" @cancel="isShowGenetic=false" @run="genetic_calculate()">

      <el-form ref="form" :model="genetic_config" label-width="170px">
        <el-form-item label="Number of iterations">
          <el-input v-model="genetic_config.iterations"></el-input>
        </el-form-item>
        <el-form-item label="Population size">
          <el-input v-model="genetic_config.size"></el-input>
        </el-form-item>
        <el-form-item label="Probability of crossover">
          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="When two individuals are selected for mating.">
            <el-input slot="reference" v-model="genetic_config.crossover"></el-input>
          </el-popover>
        </el-form-item>
        <el-form-item label="Probability of mutation">

          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="Probability of change single single chromosome during mutation to prevent local minima.">
            <el-input slot="reference" v-model="genetic_config.mutation"></el-input>
          </el-popover>
        </el-form-item>
      </el-form>

    </MessageBox>

    <traffic-list />

     
  </div>
</template>
<style  lang="scss">
@import '../scss/base.scss';

.list-traffic {
  .calculation {
    margin: 5px;
  }
  


}
</style>
<script>
import { sign_groups } from '../js/signs.js';
import { ServerBus } from './ServerBus.js';
import MessageBox from './MessageBox';
import TrafficList from './TrafficList';

export default {
  components: {
    'MessageBox': MessageBox,
    'traffic-list': TrafficList
  },
  data() {
    return {
      isShowGenetic: false,
      sign_groups: sign_groups,
      ServerBus: ServerBus,
      genetic_config: {
        "iterations": 100
        , "size": 20
        , "crossover": 0.4
        , "mutation": 0.3
      },
    }
  },
  methods: {
    traffic_load(best_solution) {
      ServerBus.$emit('traffic-load', ServerBus.best_solution);
    },
    traffic_simple(group) {
      ServerBus.$emit('traffic-simple',group, true);
    },
    traffic_light(group) {
      ServerBus.$emit('traffic-light',group, true);
    },
    traffic_dialog_update(sign) {
      ServerBus.$emit('traffic-light',sign.group, true);
      ServerBus.$emit('view-traffic',sign);
    },

    
    genetic_show() {
      ServerBus.$emit('view-genetic', this.genetic_config);
      // ServerBus.$emit('genetic-calculate');
    },

    genetic_calculate() {
      ServerBus.$emit('view-genetic', this.genetic_config);
      ServerBus.$emit('genetic-calculate');
    },

    
   
    
  }
}
</script>
