import Genetic from './genetic-org'
var genetic = Genetic.create();
import sim from '../js/simulation.js';
import sim63 from '../js/simulation63.js';


const delay = ms => {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

genetic.optimize = Genetic.Optimize.Minimize;
genetic.select1 = Genetic.Select1.Tournament2;
genetic.select2 = Genetic.Select2.Tournament2;

genetic.get_val = function(array, i) { //<if there is i modulo 
  // console.log(i);
  if(i%3==2) {  // offset
    let max = array[i-1] + array[i-2]+3; //green + red + 4
    return Math.floor(Math.random() * max)
  } else { //green or red
    return 20+Math.floor(Math.random() * 60);
  }
}

genetic.model = sim
genetic.model63 = sim63

genetic.seed = function () {
  let g = this
  function randomString(len) {
    var arr = []
    for (var i = 0; i < len; i++) {
      arr.push(g.get_val(arr,i))
    }
    return arr;
  }

  var rs = randomString(genetic.userData.length);

  //  console.log(rs);

  return rs
};

genetic.mutate = function (entity) {
  function replaceAt(str, index, character) {
    var sl = str.slice(0)
    sl[index] = character
    return sl
  }
  // chromosomal drift
  var i = Math.floor(Math.random() * entity.length)

  return replaceAt(entity, i, this.get_val(entity,i));
};

genetic.crossover = function (mother, father) {
  // two-point crossover

  // genetic.c
  var len = mother.length;
  var ca = Math.floor(Math.random() * len);
  var cb = Math.floor(Math.random() * len);
  if (ca > cb) {
    var tmp = cb;
    cb = ca;
    ca = tmp;
  }

  var son = father.slice(0, ca).concat(mother.slice(ca, ca + cb - ca)).concat(father.slice(cb));
  var daughter = mother.slice(0, ca).concat(father.slice(ca, ca + cb - ca)).concat(mother.slice(cb));

  for(let i=0;i<len;i++) {
    if(i%3==2) {  // offset
      let max = son[i-1] + son[i-2]+3; //green + red + 4
      if(son[i]>max) son[i] = max;

      let maxd = daughter[i-1] + daughter[i-2]+3; //green + red + 4
      if(daughter[i]>maxd) daughter[i] = max;
    }
  }


  return [son, daughter];
};

function generate_entity(entity, org_pop, copy_check) {
  let pop = new Array(63);
  let entity_i = 0;
  for(let i=0;i<63;i++) {
    if(copy_check[i]==1) {
      pop[i] = entity[entity_i];
      entity_i+=1;
    } else {
      pop[i] = org_pop[i];
    }
  }

  return pop;
}

genetic.fitness = function (entity) {
  var pop = generate_entity(entity, 
    genetic.userData.org_pop, 
    genetic.userData.copy_check);
//  console.log(entity, pop);


  return this.model63.predict(pop);
};

genetic.generation = function (pop, generation, stats) {
  // stop running once we've reached the solution
  return true; // pop[0].entity != this.userData["solution"];
};

genetic.run =  function( on_finish, config , on_notify, 
  length = 63, 
  org_pop = [], 
  copy_check = []) {
  // genetic.simulation.predict(pop)
  genetic.notify = async function(i,iterations,pop) {
    // console.log(pop);

    let pop_entity = generate_entity(pop.entity, 
      genetic.userData.org_pop, 
      genetic.userData.copy_check);
    
    await on_notify(i,iterations, {
      fitness: pop.fitness,
      entity: pop_entity
    });    
    await delay(1);

  }

  genetic.notification =  function(pop, generation, stats, isFinished) {
    let pop_entity = generate_entity(pop[0].entity, 
      genetic.userData.org_pop, 
      genetic.userData.copy_check);
    
    on_finish(isFinished, 
      pop_entity, 
      pop[0].fitness, 
      pop, generation, stats)
  };
  var userData = {
    "org_pop": org_pop,
    "copy_check": copy_check,
    "length": length,
    "max_val": 120
  };

  // console.log(userData);
  // return;

  genetic.evolve(config, userData)
}


export default genetic