<template>
  <div class="right-pane">
    <div class="info">

      <el-popover class="traffic-score" placement="left" title="" width="200" trigger="hover" content="Traffic score: the total time of waiting on a red signal for the current signal setting configuration">


        <el-button :disabled="ServerBus.is_change_view_disabled" class="traffic-score" slot="reference" type="primary" plain @click="view_set('view-list-traffic')">
          <i class="fas fa-trophy"></i> Traffic:
          
          <transition  name="slide-fade" mode="out-in">
            <span :key="ServerBus.traffic_score">
            {{Math.round(ServerBus.traffic_score,2)}} s
            </span>
            </transition>

  
          </el-button>
      </el-popover>
      


  <el-popover
  class="traffic-score"
    placement="left"
    title=""
    width="200"
    trigger="hover"
    :content="ServerBus.is_simulation_signs_equal ? 'Simulation Panel' : 'The score have been downloaded for different settings that current'">
     

     <el-button  :type="ServerBus.is_simulation_signs_equal ?  'success' : 'danger'" slot="reference"  :disabled="ServerBus.is_change_view_disabled"
      plain @click="simulation_open">
        <i class="fas fa-car">
      </i> Simulation 
      
      <span v-if="ServerBus.simulation_score!=0">{{ServerBus.simulation_score}} s </span></el-button>
  </el-popover>     
  <el-badge :value="ServerBus.simulation_time" id="badge-time">
            <el-button style="margin-left: -28px;border: 1px solid white;"  v-if="ServerBus.isSimulation_play" type="primary"  @click="simulation_pause" :disabled="ServerBus.is_change_view_disabled" circle><i class="fas fa-pause"></i></el-button>
            <el-button style="margin-left: -28px;border: 1px solid white;" v-else type="success" icon="el-icon-caret-right" @click="simulation_play" :disabled="ServerBus.is_change_view_disabled" circle></el-button>
</el-badge>

<el-popover
      placement="left"
    title=""
    width="200"
    trigger="hover"
    content="Information panel">
       <el-button :disabled="ServerBus.is_change_view_disabled" 
       slot="reference"
       style="margin-left: 5px;border: " type="primary" id="info-button" plain @click="view_set('view-info')">
        <i class="el-icon-question"></i> Info</el-button>

</el-popover>

<el-popover
    placement="left"
    title=""
    width="200"
    trigger="hover"
    content="Go to homepage">
             
      <el-button type="primary"  slot="reference" style="margin-left: -3px;border: 1px solid white;" id="home-button" circle @click="router_home"><i class="fas fa-home"></i></el-button>
</el-popover>
    </div>
    <div class="content">
      <div class="loader" v-if="ServerBus.is_change_view_disabled" ></div>
      <component v-bind:is="current_view" :data="data"/>
    </div>
  </div>
</template>
<script>
import {ServerBus} from './ServerBus.js'
import ViewInfo from './ViewInfo';
import ViewTraffic from './ViewTraffic'
import ViewListTraffic from './ViewListTraffic'
import ViewGenetic from './ViewGenetic'
import ViewSimulation from './ViewSimulation'

export default {
  data() {
    return {
      current_view: 'view-info',
      data: '',
      ServerBus: ServerBus
    }
  },
  components: {
    'view-info': ViewInfo,
    'view-traffic': ViewTraffic,
    'view-list-traffic': ViewListTraffic,
    'view-genetic': ViewGenetic,
    'view-simulation': ViewSimulation
  },
  created() {
    ServerBus.$on('view-traffic',this.view_traffic);
    ServerBus.$on('view-genetic',this.view_genetic);
  },
  methods: {
    router_home() {
      // window.location.href = ;
      window.open(
          'http://tensortraffic.com',
          '_blank' // <- This is what makes it open in a new window.
          );
    },
    simulation_open() {
       this.view_set('view-simulation');
    },


    view_set(name, data = '') {
      if(ServerBus.is_change_view_disabled) return;
      
      this.data = data;
      this.current_view = name
    },

    view_genetic(data) {
      this.view_set('view-genetic', data);
      // this.data = data;
      // this.current_view = 'view-genetic'
    },


    view_traffic(sign) {
       this.view_set('view-traffic', sign.group);

      // console.log(sign)
      // this.data = sign.group;
      // this.current_view = 'view-traffic';
    },

     simulation_pause() {
       ServerBus.isSimulation_play = false;
     },

    async simulation_play() {
      const delay = ms => new Promise(res => setTimeout(res, ms));

      if(ServerBus.simulation_markers==undefined
      || ServerBus.is_simulation_downloaded==false
      || ServerBus.is_simulation_signs_equal==false
      ) { //we don't yet download simulation, we have different sign signals
        //download simulation
        // await delay(500);
        if(this.current_view=='view-simulation') {
          return ServerBus.$emit('simulation-download');
        } else {
          return this.view_set('view-simulation',true);
        }
        return;
      } else {
        ServerBus.isSimulation_play = true;

        let size = ServerBus.simulation_markers==undefined ? 600 :  ServerBus.simulation_markers.size();
          for(let i = ServerBus.simulation_time;i<size;i++){
            if(!ServerBus.isSimulation_play) return;
            ServerBus.simulation_time = i;
            ServerBus.$emit('traffic-set', i);
            ServerBus.$emit('simulation-traffic', i);

            if(ServerBus.simulation_markers!=undefined)
            {
               await ServerBus.simulation_markers.car_set(i);
            }
            await delay(10);
        }
        ServerBus.isSimulation_play = false;
      }
      return;
    },



  }
}
</script>
<style  lang="scss">
@import '../scss/base.scss';

#badge-time {
  .el-badge__content {
    top: 5px;
    right: 15px;
  }
}

.slide-fade-enter-active {
  transition: all .1s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  // transform: translateX(10px);
  // transform: scale(1.5);
  opacity: 0.5;
  // font-size: 2rem;
  color: red;

}


.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

.right-pane {
 position: absolute;
 padding: 6px;
 right: 0px;
 top: 0px;
 bottom: 0px;
 z-index: 1000;
 background: rgba(255, 255, 255, 1.0);
 color: black;

  overflow-y: hidden;
  overflow-x: visible;
  width: 30%;
  min-width: 550px;
  max-width: 580px;

    hr {
    // color: black;
    border-color: silver;
  }

  .info {
    margin-bottom: 5px;
    margin-right: 40px;

  }

  padding-bottom: 80px;

  .content {
    padding: 15px;
    height: 100%;
    overflow-y: auto;
    // height: 80vh;
    margin-bottom: 250px;
  
  }
.content::-webkit-scrollbar {
  width: 10px;
}
 
.content::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 20px;
}

.content::-webkit-scrollbar-track {
  background: #ddd;
  border-radius: 20px;
}
  
}


</style>