<template>
  <div class="traffic-list">
       <div class="light" v-for="group in ServerBus.sign_groups" :key="group.id" 
      :class="{
        'selected': ServerBus.pop_over == group,
        'is_lighting': group.is_lighting,
        'is_genetic': is_genetic && group.is_genetic == false
        }"
      @click="traffic_dialog_update(group.sign_marker)" 
      @mouseover="traffic_light(group)" 
      @mouseout="traffic_simple(group)"
      
      >

      <div class="first-row">
          <el-checkbox v-if="is_genetic" v-model="group.is_genetic"></el-checkbox>

          <div class="text" @click.self="traffic_dialog_update(group.sign_marker)">
                    {{group.name}}:
                  </div>
<transition  name="slide-fade" mode="out-in">
                  <span :key="group.time_green" class="time_green ">{{group.time_green}}</span>
</transition>
                          <span class=" color-purple"  >
                    <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
                  </span>
                  <transition  name="slide-fade" mode="out-in">
                  <span :key="group.time_red" class="time_red">{{group.time_red}}</span>
                        </transition>
                        <span class="color-blue">
                    <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
                  </span>
                  <transition  name="slide-fade" mode="out-in">
                                      <span  :key="group.time" class="time">{{group.time}}</span>
                     </transition>                 

      </div>


      
      <div class="second-row" >

        <i class="fas fa-circle speedcr"
        
        :style="{
          'color': group.avg_speed_color
          }"></i>


<el-popover
    placement="top-start"
    title="Average Speed"
    width="200"
    trigger="hover"
    content="Average Speed for cars">
        <div slot="reference" 
         
        :style="{
          'color': group.avg_speed_color
          }"
        >
          <span class="text">km/h</span>
          <!-- <i class="fas fa-shipping-fast fa-2x"></i> -->
          <span class="text">{{group.avg_speed}}</span>
        </div>
  </el-popover>

    <el-popover
    placement="top-start"
    title="Number of cars"
    width="200"
    trigger="hover"
    content="Average Number of nearest cars">
        <div slot="reference" class="color-blue">
          <i class="fas fa-car"></i>
          <span class="text">{{group.avg_cars}}</span>
        </div>
  </el-popover>

  
<el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">
        <div slot="reference" class="color-red2">
          <i class="fas fa-car "></i>
          <span class="text">{{group.avg_waiting}}</span>
        </div>
  </el-popover>

        
      </div>

<!--- SECOND DOWNLOADED TO COMPARE -->
<!-- {{ServerBus.is_simulation_downloaded2}} -->
      <div class="second-row" v-if="ServerBus.is_simulation_downloaded2">

        <i class="fas fa-circle speedcr"
        
        :style="{
          'color': group.avg_speed_color2
          }"></i>

<el-popover
    placement="top-start"
    title="Average Speed"
    width="200"
    trigger="hover"
    content="Average Speed for cars">
        <div slot="reference" 
         
        :style="{
          'color': group.avg_speed_color2
          }"
        >
          <span class="text">km/h</span>
          <!-- <i class="fas fa-shipping-fast fa-2x"></i> -->
          <span class="text">{{group.avg_speed2}}</span>
        </div>
  </el-popover>

    <el-popover
    placement="top-start"
    title="Number of cars"
    width="200"
    trigger="hover"
    content="Average Number of nearest cars">
        <div slot="reference" class="color-blue">
          <i class="fas fa-car"></i>
          <span class="text">{{group.avg_cars2}}</span>
        </div>
  </el-popover>

  
<el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">
        <div slot="reference" class="color-red2">
          <i class="fas fa-car "></i>
          <span class="text">{{group.avg_waiting2}}</span>
        </div>
  </el-popover>

        <!-- <el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">

<div slot="reference"> -->
              (  <span class="time_green ">{{group.time_green2}}</span>
                <span class=" color-purple"  >
          <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
        </span>
        <span class="time_red">{{group.time_red2}}</span>
              <span class="color-blue">
          <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
        </span>
          <span class="time">{{group.time2}}</span>)
</div>
        <!-- <span>Second:</span> -->


  <!-- </el-popover> -->


      </div>



  </div>
</template>
<style  lang="scss">
@import '../scss/base.scss';


.traffic-list .light {
  .slide-fade-enter-active {
  transition: all .1s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  // transform: translateX(10px);
  // transform: scale(1.5);
  opacity: 0.5;
  // font-size: 2rem;
  color: red;

}





    cursor: pointer;
    border: 1px solid $grey-light;
    margin: 5px;
    border-radius: 5px;
    padding: 2px;
    padding-left: 10px;

    &.is_lighting {
      background-color: rgba(red,0.3) !important;
    }

    &.is_genetic {
      background: rgba(green,0.3) !important;
    }

    .first-row {
          display: flex;
    // justify-content: space-between;
    flex-wrap: nowrap;
    flex-direction: row;

      >* {
        text-align: left;
        vertical-align: middle;
        padding: 0;
        margin: 0;
        // height: 33px;
        line-height: 33px;
        margin-left: 5px;
        cursor: pointer;
        margin-right: 5px;
      }
    }

    .second-row {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: row;

      justify-content: start;

      // > span {
      //   width: 90px;
      // }

// .speedcr {
//   // margin-right: 2px;
// }
      .inside {
        width: 100px;
      }

      .text {
        font-weight: bold;
        font-size: 1em;
        line-height: 1em;
        height: 1em;
        margin: 15px;
      }

    }

    &.selected {
      background: $blue-light;
      color: white;
      >* {
        color: white !important;
      }
    }

   
    .inside-icon {
      width: 100%;
      text-align: right;
      color: #b3d8ff;
    }

    .text {
      // wit
      width: 80%;
    }
    .time {
      width: 40px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: black;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
    }

    .time_red {
      width: 30px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: $blue;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
    }

    .time_green {
      width: 30px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: $purple;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
      
    }

    &:hover {
      background: $blue-light;
      color: white;
      >* {
        color: white;
      }
    }
  }

</style>
<script>
import { ServerBus } from './ServerBus.js';

export default {
  props: {
    is_genetic: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
     ServerBus: ServerBus
    }
  },
  methods: {
    traffic_load(best_solution) {
      ServerBus.$emit('traffic-load', ServerBus.best_solution);
    },
    traffic_simple(group) {
      ServerBus.$emit('traffic-simple',group, true);
    },
    traffic_light(group) {
      ServerBus.$emit('traffic-light',group, true);
    },
    traffic_dialog_update(sign) {
      if(this.is_genetic) return;
      ServerBus.$emit('traffic-light',sign.group, true);
      ServerBus.$emit('view-traffic',sign);
    },
    
  }
}
</script>
