<template>
  <div class="genetic-view">
    <h1>Genetic Algorithm</h1>

    <h2>{{isLoading_text}}</h2>

    <strong>Parameters of a genetic algorithm:</strong>

     <el-form ref="form"  :model="genetic_config" label-width="170px">
        <el-form-item label="Number of iterations">
          <el-input v-model="genetic_config.iterations"></el-input>
        </el-form-item>
        <el-form-item label="Population size">
          <el-input v-model="genetic_config.size"></el-input>
        </el-form-item>
        <el-form-item label="Probability of crossover">
          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="When two individuals are selected for mating.">
            <el-input slot="reference" v-model="genetic_config.crossover"></el-input>
          </el-popover>
        </el-form-item>
        <el-form-item label="Probability of mutation">

          <el-popover class="traffic-score" placement="right" title="" width="200" trigger="hover" content="Probability of change single single chromosome during mutation to prevent local minima.">
            <el-input slot="reference" v-model="genetic_config.mutation"></el-input>
          </el-popover>
        </el-form-item>

          <el-button type="primary" :disabled="isLoading" @click="genetic_calculate" plain>Run Genetic Algorithm</el-button>

    </el-form>


    <hr/>

      <strong class="score-header">
        <strong class="score">{{isLoading_score}}</strong>
      </strong>
      <canvas id="myChart" width="500" height="170"></canvas>

<div class="info">
  Uncheck Sign Group to exclude them from the calculations.
</div>
<traffic-list :is_genetic="true"/>
  </div>
</template>
<style lang="scss">
@import '../scss/base.scss';

.genetic-view {

  .info {
    
    border: 1px solid $grey-light;
    color: $blue;
    margin: 5px;
    border-radius: 5px;
    padding: 2px;
    padding-left: 10px;

  }
}
</style>

<script>
import genetic from '../js/genetic.js'
import { ServerBus } from './ServerBus.js';
import TrafficList from './TrafficList';


const delay = ms => {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

export default {
  components: {
    'traffic-list': TrafficList
  },
  // props: ['data'],
  data() {
      return {
              myChart: '',
              isLoading_text: '',
              isLoading_score: 0,
              isLoading: false,
              genetic_config: {
                  "iterations": 100
                  , "size": 20
                  , "crossover": 0.4
                  , "mutation": 0.3
              },
      }
  },

/***
 * What to do
 */
  mounted() {
    // console.log('start genetic algorithm');
    // this.init_chart();
    // this.genetic_calculate();
  },


  methods: {
    init_chart() {
       var ctx = document.getElementById("myChart");
       this.myChart = new Chart(ctx, {
            labels: ['Traffic score'],
            borderWidth: 1,
            type: 'line',
            data: {
              datasets: [
                {
                  backgroundColor: 'rgba(255,99,132,1)',
                  borderColor: 'rgba(255,99,132,1)',
                  label: 'Traffic score',
                  labels: [],
                  data: []
                },
                {
                  backgroundColor: 'rgba(255,99,132,1)',
                  borderColor: 'rgba(255,99,132,1)',
                  label: 'Traffic score 2nd score',
                  labels: [],
                  data: []
                }
              ]
            },
            options: {
              // title: {
              //   display: true,
              //   text: 'Traffic score'
              // },
              legend: {
                display: false,
                labels: {
                    fontColor: "#000080",
                }
              },
                scales: {
                    xAxes: [{
                        display: false,
                        scaleLabel: {
                          display: false,
                          labelString: 'Iteration'
                        },
                        ticks: {
                          stepSize: 10
                        }

                    }],
                  yAxes: [{
                              display: true,
                               beginAtZero: true,
                        
                              scaleLabel: {
                                display: false,
                                labelString: 'Traffic Score'
                              },
                             
                            }]                    
                }
            }
        });
     },

      chart_add(label, val) {
       this.myChart.data.labels.push(label)
       this.myChart.data.datasets[0].data.push(val)
       this.myChart.update()
     },
     traffic_clear() {
           for(const key in ServerBus.sign_groups) {
             let group = ServerBus.sign_groups[key]
            ServerBus.$emit('traffic-simple', group, false);
           }
     },

      traffic_show(pop, show = false) {
            for(const key in ServerBus.sign_groups) {
                let group = ServerBus.sign_groups[key]
                let val =  pop[group.id*3+2]
                let val_green =  pop[group.id*3]
                let val_red =  pop[group.id*3+1]

                let time = parseInt(val)
                let time_red = parseInt(val_green)
                let time_green = parseInt(val_red)
                
                if(
                  group.time!=time
                ||group.time_red!=time_red
                ||group.time_green!=time_green
                ) {
                    // console.log(group, 
                    // group.time,
                    // time,
                    // group.time_red,
                    // time_red,
                    // group.time_green,
                    // time_green);
                    group.changeTime = Date.now()
                    group.time = time
                    group.time_green = time_green
                    group.time_red = time_red
                    
                    ServerBus.$emit('traffic-light', group, false);
                } else  if(group.changeTime!=undefined && Date.now()-group.changeTime > 2000) {
                    group.changeTime = undefined
                    // this.traffic_set_single(group);
                    ServerBus.$emit('traffic-simple', group, false);
                }
              }
    },

      async genetic_calculate() {
      // this.isSimulation_open = false;
      // this.isLoading_finish = false
      this.isLoading_text = 'Running'
      this.isLoading = true
      ServerBus.is_change_view_disabled = true;
      // console.log(this.data);
      
      // this.isLoading_open = true
      // this.isShowGenetic = false
      // this.panelType = ''
 
      // this.map.panTo(
      //   [52.2176205
      //   ,20.9740354
      //   ])
      await delay(500)
      this.init_chart()
      // this.chart_add(0,this.time)

      let v = this
      let result = []
      try {
           let org_pop = new Array(63);
           let copy_check = new Array(63);
          //  for(let i=0;i<63;i++) {
          //    org_pop[i] = 0;
          //    conf_pop[i] = 0;
          //  }
            let size = 0;
           for(const key in ServerBus.sign_groups) {
                let group = ServerBus.sign_groups[key]

                org_pop[group.id*3+2] = group.time;
                org_pop[group.id*3] = group.time_green;
                org_pop[group.id*3+1] = group.time_red;
                
                if(group.is_genetic) {
                  size += 1;
                  copy_check[group.id*3+2] = 1;
                  copy_check[group.id*3] = 1;
                  copy_check[group.id*3+1] = 1;

                } else {
                  copy_check[group.id*3+2] = 0;
                  copy_check[group.id*3] = 0;
                  copy_check[group.id*3+1] = 0;
                }

           }

          //  console.log(org_pop, conf_pop, size);
          //  return;
        

          await genetic.run(function(
            isFinished, 
            result,
            fitness, 
            pop, generation, stats) { 
            if(isFinished) {
                v.isLoading = false
                ServerBus.is_change_view_disabled = false;
                v.isLoading_text = "Finished calculation"
                // v.traffic_load(result, true, false)
                v.traffic_clear();
                // v.traffic_set(0);

                let r = genetic.model63.predict(result);
                ServerBus.$emit('traffic-load', result);
                // console.log(r);

                // console.log(r, result);

                if(r<ServerBus.best_solution_result) {
                  ServerBus.best_solution_result = r;
                  ServerBus.best_solution = result;
                }
            }
          },v.genetic_config, async function(i,max,best) {
            // console.log(best);

            v.chart_add(i, best.fitness)
            // console.log(best);

            v.traffic_show(best.entity, i%6==0)
            v.isLoading_text = "Running: " + i + "/" + max 
            // ServerBus.$emit('traffic-load', best.entity);
            v.isLoading_score = String(Math.round(best.fitness)) + " s"

            await delay(1)
          },size*3, org_pop, copy_check)
      }  catch (error) {
        }
     },
  }
}
</script>
