      <!-- <input type="file" id="logo"> -->
      <!-- <el-input placeholder="Please input" type="file" v-model="speed_text"></el-input> -->
      <!-- A{{speed_text}}A -->
      <!-- <el-button-group style="margin-left: 5px; margin-right: 5px;"> -->
        <el-tooltip content="Export Simulation" placement="top" effect="light">  <span >
              <el-button @click="simulation_export" type="primary"  :disabled="!isSumulation_finish" plain  icon="el-icon-upload2" ></el-button>
</span>
        </el-tooltip>
        <el-tooltip content="Import Simulation" placement="top" effect="light">  <span >
          <el-button @click="simulation_import" type="primary"  :disabled="isSumulation_loading" plain icon="el-icon-download"></el-button>
        </span>
        </el-tooltip>
<el-tooltip content=" Import To Compare" placement="top" effect="light">    <span >
        <el-button @click="simulation_import_compare " type="warning"  :disabled="!isSumulation_finish" plain icon="el-icon-sort"></el-button>
</span>
</el-tooltip>

<!-- <el-tooltip content="Traffic Statistics" placement="top" effect="light">        <span >
        <el-button @click="isSimulation_statistics_open = true" type="warning"  :disabled="!isSumulation_finish" plain ><i class="far fa-chart-bar"></i></el-button>
</span>
</el-tooltip> -->


      <div style="height: 180px">
           <canvas id="myChart_sim" width="500" height="140"></canvas>
      </div>
    </div>

  <div  class="dialog-footer">
          <div class="block" id="simulation_block">
            <!-- <el-button  v-if="ServerBus.isSimulation_play" type="primary"  @click="simulation_pause" :disabled="isSumulation_loading" round><i class="fas fa-pause"></i></el-button>
            <el-button v-else type="success" icon="el-icon-caret-right" @click="simulation_play" :disabled="isSumulation_loading" round></el-button> -->
            <el-slider @change="simulation_change" v-model="ServerBus.simulation_time" :disabled="isSumulation_loading" :min="0" :max="ServerBus.simulation_max" show-input></el-slider>
          </div>

    </div>

<hr/>

<traffic-list/>

</div>


 
</template>
<script>
import Vue from 'vue'
import { ServerBus } from './ServerBus.js';
import { memorySizeOf } from '../js/helpers.js';
import convert_text from './import.js';
import TrafficList from './TrafficList';


const delay = ms => {
  return new Promise(resolve => {
    setTimeout(resolve, ms)
  })
}

function deg2rad(deg) {
  return deg * (Math.PI/180)
}

function getDistanceFromLatLonInKm(lat1,lon1,lat2,lon2) {
  var R = 6371; // Radius of the earth in km
  var dLat = deg2rad(lat2-lat1);  // deg2rad below
  var dLon = deg2rad(lon2-lon1); 
  var a = 
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * 
    Math.sin(dLon/2) * Math.sin(dLon/2)
    ; 
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
  var d = R * c; // Distance in km
  return d;
}



function get_datetime_str() {
  var currentdate = new Date(); 
var datetime =  currentdate.getFullYear() + 
      "-" + (currentdate.getMonth()+1) + 
      "-" + currentdate.getDate() + " "
              + currentdate.getHours() + ":"  
              + currentdate.getMinutes() + ":" 
              + currentdate.getSeconds();
 return datetime;
}

const send_http = function(url,body)  {
  return new Promise(resolve => {
     var xmlhttp = new XMLHttpRequest();   // new HttpRequest instance 
     xmlhttp.timeout = 100000;
        xmlhttp.onreadystatechange = function() {
          if(this.readyState==4) {
             if ((this.status == 200 || this.status == 201)) {
                resolve(this.response);
            } else {
              // throw new TypeError('Throwing');
              resolve(JSON.stringify( {
                isError: true,
                status: this.status,
                statusText: this.statusText
              } ));
            }
          }
        };  
        xmlhttp.open("POST", url);
        xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlhttp.send(body);
  })
}

const https = require("https");
const url_compute =
  "http://35.176.210.78:25041/compute";
  const url_results =
  "http://35.176.210.78:25041/results";

export default {

  props: ['data'],
  components: {
    'traffic-list': TrafficList
  },
  data() {
    return {
      simulation: [],
      // simulation_markers: undefined,
      // simulation_time: 0,
      // isSimulation_play: false,
      

      chart_data0: [],
      chart_data1: [],


      chartSim: '',
      ServerBus: ServerBus,

      isSimulation_statistics_open: false,
      simulation_statistics_table: [],

      isSumulation_loading: false,
      isSumulation_downloading: false,
      isSumulation_loading_time: '',
      isSumulation_finish: true,
      
      isSimulation_text: 'Click run to get all simulations'
    }
  },
  // computed: {
  //   simulation_max: Server
  // },
  mounted() {
    this.simulation_open();
    ServerBus.$on('simulation-download', this.simulation_download);
    ServerBus.$on('simulation-traffic', this.simulation_traffic);
    
    if(this.data==true) {
      this.simulation_download();

    }

},

  methods: {
     simulation_open(load_data = true) {
      this.isSimulation_open = true;
      
      ServerBus.simulation_max = (ServerBus.simulation_markers!=undefined ? ServerBus.simulation_markers.get_length() : 600);
      
      // this.isSimulation_finish = true;
      
      let v =  this;
      Vue.nextTick(function () {
         v.init_chartsim(true, load_data);
         ServerBus.$emit('traffic-set', 0)
      })
    },

    car_on(c,e) {
     let v = this;
     v.speed_visible = true;
     v.speed_left = e.containerPoint.x+25;
     v.speed_top = e.containerPoint.y-20;
     v.speed_text = "Speed: " + c.speed.toFixed(2) + ' km/h';
    },

    car_off(c,e) {
     let v = this;
     v.speed_visible = false;
    },

    simulation_statistics_add(simulation) {
      for(let car of simulation) {
          let min_dist = 1000000000.000;
          let min_sign = undefined;
          for(var key in ServerBus.sign_groups) {
              let group = ServerBus.sign_groups[key];
              
              let distance = getDistanceFromLatLonInKm(
                group.sign_marker.position[0],
                group.sign_marker.position[1],
                car[2],car[3])

              if(distance<min_dist) {
                min_sign = group;
                min_dist = distance;
              }
          }
          min_sign.cars += 1;
          min_sign.total_speed += car[1];
          if(car[1]<=0) {
            min_sign.waiting_cars += 1;
          }
          min_sign.set_speed_color();
        }
    },

     simulation_statistics_add2(simulation) {
      for(let car of simulation) {
                  let min_dist = 1000000000.000;
                  let min_sign = undefined;
                  for(var key in ServerBus.sign_groups) {
                      let group = ServerBus.sign_groups[key];
                      
                      let distance = getDistanceFromLatLonInKm(
                        group.sign_marker.position[0],
                        group.sign_marker.position[1],
                        car[2],car[3])

                      if(distance<min_dist) {
                        min_sign = group;
                        min_dist = distance;
                      }
                  }
                  min_sign.cars2 += 1;
                  min_sign.total_speed2 += car[1];
                  if(car[1]<=0) {
                    min_sign.waiting_cars2 += 1;
                  }

                  min_sign.set_speed_color2();
      }
    },

    simulation_statistics_clear() {
       for(var key in ServerBus.sign_groups) {
          let group = ServerBus.sign_groups[key];
          group.cars = 0;
          group.waiting_cars = 0;
          group.total_speed = 0;

          group.set_speed_color();
      }
    },
    
    simulation_statistics_clear2() {
       for(var key in ServerBus.sign_groups) {
          let group = ServerBus.sign_groups[key];

          group.cars2 = 0;
          group.waiting_cars2 = 0;
          group.total_speed2 = 0;
          group.time2 = 0;

          let pop = ServerBus.simulation2_signals;
          group.time2 =   pop[group.id*3+2];  
          group.time_green2 =   pop[group.id*3]; 
          group.time_red2 =   pop[group.id*3+1]; 

          // group.time2 = ServerBus.simulation2_signals[group.id];
          group.set_speed_color2();
      }
    },

    
    async simulation_statistics_open() {
      this.simulation_statistics_table = [];
      this.isLoading_screenText = "Please Wait. Calculating Statistics..."
      this.isLoading_screen = true;
      ServerBus.is_change_view_disabled = true;
      
      await delay(200);

      try {
        for(var key in ServerBus.sign_groups) {
          let group = ServerBus.sign_groups[key];
          group.cars = 0;
          group.waiting_cars = 0;
          group.total_speed = 0;

          group.cars2 = 0;
          group.waiting_cars2 = 0;
          group.total_speed2 = 0;
          group.time2 = 0;

          // if(ServerBus.simulation2_score>0) {
          //   group.time2 = ServerBus.simulation2_signals[group.id];
          // }
      }

      let traffics = [];
      

      for(let i=0;i<ServerBus.simulation.length;i++) {
        // let r = ; //each simulation statistics
        for(let car of ServerBus.simulation[i]) {
          let min_dist = 1000000000.000;
          let min_sign = undefined;
          for(var key in ServerBus.sign_groups) {
              let group = ServerBus.sign_groups[key];
              
              let distance = getDistanceFromLatLonInKm(
                group.sign_marker.position[0],
                group.sign_marker.position[1],
                car[2],car[3])

              if(distance<min_dist) {
                min_sign = group;
                min_dist = distance;
              }
          }
          min_sign.cars += 1;
          min_sign.total_speed += car[1];
          if(car[1]<=0) {
            min_sign.waiting_cars += 1;
          }
        }
      }

        if(ServerBus.simulation2_score>0) {
          for(let i=0;i<ServerBus.simulation2.length;i++) {
                // let r = ; //each simulation statistics
                for(let car of ServerBus.simulation2[i]) {
                  let min_dist = 1000000000.000;
                  let min_sign = undefined;
                  for(var key in ServerBus.sign_groups) {
                      let group = ServerBus.sign_groups[key];
                      
                      let distance = getDistanceFromLatLonInKm(
                        group.sign_marker.position[0],
                        group.sign_marker.position[1],
                        car[2],car[3])

                      if(distance<min_dist) {
                        min_sign = group;
                        min_dist = distance;
                      }
                  }
                  min_sign.cars2 += 1;
                  min_sign.total_speed2 += car[1];
                  if(car[1]<=0) {
                    min_sign.waiting_cars2 += 1;
                  }
                }
              }
        }

      } finally {
        this.isLoading_screen = false;
        ServerBus.is_change_view_disabled = false;
      }

    },


    simulation_init() {
        ServerBus.simulation = [];
        if(ServerBus.simulation_markers!=undefined)
        {
          ServerBus.simulation_markers.clear_cars();
        }
        ServerBus.simulation_markers = L.Marker.movingMarker(
          ServerBus.map, 
          this.simulation, 
          this.car_on, 
          this.car_off);
    },

    
    get_avg_speed(res) { 
      let sum = 0;
      let length = res.length;
      for(let r of res) {
         sum+= r[1];
      }
      return  Math.round( sum/length * 100) / 100;
    },
    
    
    async simulation_loop_status(xmlhttp) {
        while(this.isSumulation_downloading) {
          if(this.isSumulation_finish)  return;
          this.isSimulation_text = "Waiting for calculations: " +
            Math.round((Date.now()-this.isSumulation_loading_time)/1000) + "s";
          await delay(500);
        }
   },

    simulation_restore() {
      ServerBus.$emit('traffic-load',ServerBus.simulation_configuration,true,true);
    },
   
   async simulation_download() {
      ServerBus.is_change_view_disabled = true;
      this.isSumulation_loading = true;


      let settings = JSON.stringify({
        "settings": JSON.stringify(ServerBus.sign_times)
      });

      console.log('settings', settings);

      ServerBus.simulation_time = 0;
      this.isSumulation_loading = true;
      this.isSumulation_downloading = true;
      this.isSumulation_loading_time = Date.now();
      this.isSumulation_finish = false;

      this.init_chartsim(true);
      let v = this;

      try {
        let v = this;
        var xmlhttp = new XMLHttpRequest();   // new HttpRequest instance 
        xmlhttp.timeout = 100000;
        xmlhttp.onreadystatechange = function() {
          if(this.readyState == 4) {
             if ((this.status == 200 || this.status == 201)) {

                  var status = JSON.parse(this.response);

                  // console.log(this.response);
                  v.isSumulation_downloading = false;
                  v.simulation_score = status.score;
                  ServerBus.simulation_score = status.score;
                  ServerBus.simulation_configuration = ServerBus.sign_times.slice(0);// JSON.stringify(ServerBus.sign_times);

                  v.simulation_download_times();
              } else {
              //  console.log(this);
               v.$notify({
                    title: 'Errors during calculation',
                    message:  this.status + ":" + this.statusText,
                    type: 'danger',
                    position: 'top-left'
                  });
                v.stop_download("Error during calculation", 0, "error");
                return;
              }
          }
        };  
        xmlhttp.open("POST", url_compute);
        xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xmlhttp.send(settings)
        this.simulation_loop_status(xmlhttp);
        await delay(500);

      } catch(error) {
        // console.log(error);
      this.$notify({
          title: 'Errors during calculation',
          message:  error,
          type: 'danger',
          position: 'top-left'
        });
        v.stop_download("Error during calculation", 0,  "error");
        return;
      }


    },


 async simulation_download_times() {
      let v = this;
      ServerBus.simulation = [];
      v.isSimulation_text = "Download simulations: ";
      ServerBus.simulation_time = 0;
      ServerBus.is_simulation_downloaded = true;
      v.simulation_statistics_clear();

      ServerBus.$emit('traffic-load',ServerBus.sign_times,true,true);


      v.simulation_init();
      v.chartsim_set(ServerBus.simulation_score);

      this.$notify({
          title: 'Success',
          message: 'Simulation calculated, start downloading (score: ' + ServerBus.simulation_score  + ' s)',
          type: 'success',
          position: 'top-left'
        });

      var folder_text = "output";
      for(let s of ServerBus.sign_times) {
        folder_text += "_" + String(s);
      }

      // console.log(folder_text);
      
      for(let i=0;i<600;i++) {
        try {
            this.isSimulation_text = "Download simulations: " + (i+1) + "/600";
            var time = '{"time":"' + String(i) +'","outdir": "'  + folder_text + '"  }';
            let res = JSON.parse(await send_http(url_results,time));
            if(res.isError!=undefined) {
               if(!v.isSumulation_loading) return;
                v.$notify({
                    title: 'Error while downloading / Software will try repeat after 5 seconds. Please click "cancel" to stop.',
                    message: res.statusText,
                    type: 'error',
                    position: 'top-left'

                  });
                  await delay(5000);
                  i = i-1;
             
              continue; 
            }
            res = convert_text( res.cars);
          
            ServerBus.simulation_markers.add_values(res);
            let avg_speed = this.get_avg_speed(res);
            this.chartsim_add(i,avg_speed);

            ServerBus.simulation.push(res);
            v.simulation_statistics_add(res);

            ServerBus.simulation_time = i;
            ServerBus.$emit('traffic-set', i);
            v.simulation_traffic(i);
            
            await ServerBus.simulation_markers.car_set(i);
            

            if(!v.isSumulation_loading) return;
            // await delay(10);

        } catch(error) {
          // console.log(error);
          this.$notify({
            title: 'Errors during download',
            message:  error,
            type: 'danger',
            position: 'top-left'
          });
          // console.error('Errors during download', error);
        }
      }
      v.stop_download("Simulation run", 0);
   },

   async stop_download(text, time, type="success") {
      this.isSimulation_text = "Simulation";
      this.isSumulation_finish = true;
      this.isSumulation_loading = false;
      ServerBus.simulation_time = time;
      ServerBus.is_change_view_disabled = false;


      if(ServerBus.simulation_markers!=undefined)
      {
        await  ServerBus.simulation_markers.car_set(time);
      }
      ServerBus.$emit('traffic-set', time);
      this.$notify({
          title: type,
          message: text,
          type: type,
          position: 'top-left'
        });

      ServerBus.simulation_chartdata = this.myChart_sim.data;
      ServerBus.simulation_max = (ServerBus.simulation_markers!=undefined ? ServerBus.simulation_markers.get_length() : 600);
      ServerBus.is_change_view_disabled = false;
   },
   

    simulation_import() {
     let v = this;
    //  ServerBus.is_change_view_disabled = true;
     var element = document.createElement('div');
     element.innerHTML = '<input type="file" accept=".json">';
     var fileInput = element.firstChild;

     fileInput.addEventListener('change', function() {
          var file = fileInput.files[0];

          if (file.name.match(/\.(xml|txt|json)$/)) {
              ServerBus.is_change_view_disabled = true;
              var reader = new FileReader();

              reader.onload = async function() {
                ServerBus.simulation_time = 0;
                this.isSumulation_loading = true;
                this.isSumulation_downloading = true;
                this.isSumulation_loading_time = Date.now();
                this.isSumulation_finish = false;
                
                  var r = JSON.parse(reader.result);

                   ServerBus.simulation_score = r.simulation_score;
                   ServerBus.is_simulation_downloaded = true;
                   v.simulation_statistics_clear();


                   ServerBus.simulation_configuration = r.sign_times.slice(0);
                   ServerBus.$emit('traffic-load',r.sign_times,true,true);


                   v.simulation_open(false);
                   v.init_chartsim(true);
                  //  v.traffic_set(0);
                   ServerBus.$emit('traffic-set', 0);
                   v.simulation_init();
                   v.chartsim_set(r.simulation_score);

                  // v.isSimulation_text = "Please Wait. Importing simulation" ;
                  v.isLoading_screenText = "Please Wait. Importing simulation" ;
                  v.isLoading_screen = true;
                  await delay(200);    
                


                   for(let i=0;i<r.simulation_times.length;i++) {
                      let res = r.simulation_times[i];
                      // v.isSimulation_text = "Import simulations: " + (i+1) + "/600";
                      ServerBus.simulation_markers.add_values(res);
                      let avg_speed = v.get_avg_speed(res);
                      v.chartsim_add(i,avg_speed);
                      ServerBus.simulation.push(res);
                      v.simulation_statistics_add(res);

                      // await delay(10);
                   }
                   v.isLoading_screen = false;
                   v.stop_download("Simulation Imported", 0);
              };
              reader.readAsText(file);    
          } else {
              alert("File not supported, .txt or .json files only");
          }
      });
      fileInput.click();
    },

    simulation_import_compare() {
     let v = this;
     var element = document.createElement('div');
     element.innerHTML = '<input type="file" accept=".json">';
     var fileInput = element.firstChild;

     fileInput.addEventListener('change', function() {
          var file = fileInput.files[0];

          if (file.name.match(/\.(xml|txt|json)$/)) {
              var reader = new FileReader();
              ServerBus.is_change_view_disabled = true;

              reader.onload = async function() {
                  var r = JSON.parse(reader.result);
                  
                  ServerBus.simulation2 = [];
                  ServerBus.simulation2_score = r.simulation_score;
                  ServerBus.simulation2_signals = r.sign_times;
                  ServerBus.is_simulation_downloaded2 = true;
                  v.simulation_statistics_clear2();


                  v.isLoading_screenText = "Please Wait. Importing 2nd simulation" ;
                  v.isLoading_screen = true;
                  
                  // await delay(200);
                  // v.isSimulation_text = "Please Wait. Importing 2nd simulation" ;
                  
                  // console.log(r.simulation_score)

                  v.chartsim_set2(r.simulation_score);

                  await delay(200);    
                  v.myChart_sim.data.datasets[1].data.length = 0;

                  // v.chartsim_set(r.simulation_score);
                  for(let i=0;i<r.simulation_times.length;i++) {
                      let res = r.simulation_times[i];
                      let avg_speed = v.get_avg_speed(res);
                      v.chartsim_add2(i, avg_speed);
                      v.simulation_statistics_add2(res);

                      // ServerBus.simulation2.push(res);

                   }
                   v.isLoading_screen = false;
                   v.stop_download("2nd Simulation Imported", 0);
              };

              reader.readAsText(file);    
          } else {
              alert("File not supported, .txt or .json files only");
          }
      });
      fileInput.click();
    },
    
    simulation_export() {
      
      let csvContent = JSON.stringify({
         simulation_score: ServerBus.simulation_score,
         sign_times: ServerBus.simulation_configuration,
         simulation_times:  ServerBus.simulation
      });
      
      var blob = new Blob([csvContent], {type: 'text/json'});
      var filename = get_datetime_str() + "-" + String(ServerBus.simulation_score) + '-simulation.json'; // $scope.getFileNameFromHttpResponse(result);
      if(window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveBlob(blob, filename);
      }
      else{
          var elem = window.document.createElement('a');
          elem.href = window.URL.createObjectURL(blob);
          elem.download = filename;
          document.body.appendChild(elem);
          elem.click();
          document.body.removeChild(elem);
      }
      return
    },


    simulation_traffic(el) {
      if(this.myChart_sim) {
        let ann = this.myChart_sim.options.annotation.annotations[0];
        ann.value = el;

        let text = '';



        if(this.myChart_sim.data.datasets[0]) {
          let d1 = this.myChart_sim.data.datasets[0];
          text = '1st: ' + d1.data[el];
          if(el/d1.data.length>0.55) {
            ann.label.xAdjust = 50;
          } else {
           ann.label.xAdjust = -50;
        }

          
        }
        if(this.myChart_sim.data.datasets[1]) {
          let d1 = this.myChart_sim.data.datasets[1];
          let v = d1.data[el];
          if(v!=undefined) {
            text += '/ 2nd: ' +  v;
          }
        }

        text += ' km/h';

        
        ann.label.content = text;

        

        // console.log('change', ann);
        this.myChart_sim.update();
      }

    },
   
    
   async simulation_change(el) {
      // console.log(ServerBus.simulation_markers);

// console.log(el,ServerBus.simulation_max);

      ServerBus.$emit('traffic-set', el);

      if(ServerBus.simulation_markers!=undefined) {
         await ServerBus.simulation_markers.car_set(el);
      }
      this.simulation_traffic(el);
   
    },
    
    chartsim_set(score) {
       this.myChart_sim.data.datasets[0].label = "Avg Speed 1st (" + score + "s)";
       this.myChart_sim.update();
     },


    chartsim_set2(score) {
       this.myChart_sim.data.datasets[1].label = "Avg Speed 2nd (" + score + "s)";
       this.myChart_sim.update();
     },

     chartsim_add2(label,val) {
       this.myChart_sim.data.datasets[1].data.push(val);
       this.myChart_sim.update();
     },

     chartsim_add(label,val) {
       this.myChart_sim.data.labels.push(label);
       this.myChart_sim.data.datasets[0].data.push(val);
       this.myChart_sim.update();
     },

     __init_chart() {
        var ctx = document.getElementById("myChart_sim");
        this.myChart_sim = new Chart(ctx, {
            labels: ['Avg Speed'],
            borderWidth: 1,
            type: 'line',
            data: {
              datasets: [
                {
                  backgroundColor: 'rgba(0,209,178,0.3)',
                  borderColor: 'rgba(0,0,0,0)',
                  label: 'Avg Speed 1st',
                  labels: [],
                  data: []
                },
                {
                  backgroundColor: 'rgba(209,0,178,0.15)',
                  borderColor: 'rgba(0,0,0,0)',
                  label: 'Avg Speed 2nd',
                  labels: [],
                  data: []
                }
              ]
            },
            options: {
                 annotation: {
                        annotations: [
                          {
                            type: "line",
                            mode: "vertical",
                            scaleID: "x-axis-0",
                            value: 10,
                            borderColor: "red",
                            label: {
                              xAdjust: -50,
                              		fontSize: 10,


                              content: '',
                              enabled: true,
                              position: "top"
                            }
                          }
                        ]
                      },  
              maintainAspectRatio: false,
              tooltips: {
                callbacks: {
                  label: function(tooltipItem, data) {
                    return tooltipItem.yLabel + ' km/h';
                  }
                }
              },
              legend: {
                display: true,
                labels: {
                    fontColor: "#000080",
                },
                position: 'bottom'
              },
                scales: {
                    xAxes: [{
                        display: false,
                        scaleLabel: {
                          display: false,
                          labelString: 'Time'
                        },
                        ticks: {
                          stepSize: 10
                        }

                    }],
                  yAxes: [{
                              display: true,
                              beginAtZero: true,
                              min: 0,
                        
                              scaleLabel: {
                                min: 0,
                                display: true,
                                labelString: 'Average Speed (km/h)',
                              },
                              ticks: {
                                 min: 0,
                                 beginAtZero:true
                                 }
                             
                            }]   } }});
     },

     init_chartsim(force = false, load_data = false) {
      
       if(this.myChart_sim!='' && force==false) {
         return;
       }
       if(this.myChart_sim) {
         this.myChart_sim.destroy();
       }
       this.__init_chart();   

       if(load_data && ServerBus.simulation_chartdata ) {
          // console.log(ServerBus.simulation_chartdata );
          
          this.myChart_sim.data.labels = ServerBus.simulation_chartdata.labels;
          this.myChart_sim.data.datasets = ServerBus.simulation_chartdata.datasets;
          
          this.myChart_sim.update();


       }
    },

  }
}
</script>
