<template>
  <div class="single-traffic-info">
    <h2>Update an offset for a traffic signal</h2>
    <hr/>
    
<strong>Statistics from the simulation: </strong>
      <div class="second-row" >

        <i class="fas fa-circle speedcr"
        
        :style="{
          'color': data.avg_speed_color
          }"></i>


<el-popover
    placement="top-start"
    title="Average Speed"
    width="200"
    trigger="hover"
    content="Average Speed for cars">
        <div slot="reference" 
         
        :style="{
          'color': data.avg_speed_color
          }"
        >
          <span class="text">km/h</span>
          <!-- <i class="fas fa-shipping-fast fa-2x"></i> -->
          <span class="text">{{data.avg_speed}}</span>
        </div>
  </el-popover>

    <el-popover
    placement="top-start"
    title="Number of cars"
    width="200"
    trigger="hover"
    content="Average Number of nearest cars">
        <div slot="reference" class="color-blue">
          <i class="fas fa-car"></i>
          <span class="text">{{data.avg_cars}}</span>
        </div>
  </el-popover>

  
<el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">
        <div slot="reference" class="color-red2">
          <i class="fas fa-car "></i>
          <span class="text">{{data.avg_waiting}}</span>
        </div>
  </el-popover>

        
      </div>

<!--- SECOND DOWNLOADED TO COMPARE -->
<!-- {{ServerBus.is_simulation_downloaded2}} -->
      <div class="second-row" v-if="ServerBus.is_simulation_downloaded2">

        <i class="fas fa-circle speedcr"
        
        :style="{
          'color': data.avg_speed_color2
          }"></i>

<el-popover
    placement="top-start"
    title="Average Speed"
    width="200"
    trigger="hover"
    content="Average Speed for cars">
        <div slot="reference" 
         
        :style="{
          'color': data.avg_speed_color2
          }"
        >
          <span class="text">km/h</span>
          <!-- <i class="fas fa-shipping-fast fa-2x"></i> -->
          <span class="text">{{data.avg_speed2}}</span>
        </div>
  </el-popover>

    <el-popover
    placement="top-start"
    title="Number of cars"
    width="200"
    trigger="hover"
    content="Average Number of nearest cars">
        <div slot="reference" class="color-blue">
          <i class="fas fa-car"></i>
          <span class="text">{{data.avg_cars2}}</span>
        </div>
  </el-popover>

  
<el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">
        <div slot="reference" class="color-red2">
          <i class="fas fa-car "></i>
          <span class="text">{{data.avg_waiting2}}</span>
        </div>
  </el-popover>

        <!-- <el-popover
    placement="top-start"
    title="Average waiting cars"
    width="200"
    trigger="hover"
    content="Average Number of cars waiting on a red signal">

<div slot="reference"> -->
              (  <span class="time_green ">{{data.time_green2}}</span>
                <span class=" color-purple"  >
          <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
        </span>
        <span class="time_red">{{data.time_red2}}</span>
              <span class="color-blue">
          <i class=" fas fa-traffic-light" @click.self="traffic_dialog_update(group.sign_marker)"></i>
        </span>
          <span class="time">{{data.time2}}</span>)
</div>

    <hr/>
      <span><strong>{{data.name}}</strong>:  The signal starts from time 0 and its state is calculated by the offset of the value: </span>

      <el-slider class="mt-2" show-input placeholder="Please input" v-model="traffic_time" :min="0" :max="data.max_time" />

<div class="mb-2 mt-2" >
<span class="color-purple" style="display:inline-block; width: 180px;">
  Green time for traffic #1:
</span>
<el-input-number size="mini" show-input placeholder="Please input" v-model="traffic_green" :min="20" :max="80" />
</div>

<div >
    <span class="color-blue" style="display:inline-block; width: 180px;">
      Green time for traffic #2:
    </span>
    <el-input-number size="mini" show-input placeholder="Please input" v-model="traffic_red" :min="20" :max="80" />
</div>

<hr/>
      <div v-for="s in data.signs" :key="s.id">
        <span class="quote " 
        :class="{
          'color-purple': s.isGreen,
          'color-blue': !s.isGreen,
          }"
        >Traffic signal at position: {{s.position_text}} </span>
        <div class="line">
          <img class="first" src="signal.png" alt="">
          <div class="color123 " :style="{
            'width': (s.isGreen ? traffic_gtime1 : traffic_time1)/60*100 + '%',
            'background-color': s.isGreen ? '#67c23a' : '#f56c6c'
            }"> {{(s.isGreen ? traffic_gtime1 : traffic_time1)}}</div>

          <div class="color123" :style="{
            'width': 
           (s.isGreen ? traffic_gtime2 : traffic_time2)/60*100 + '%',
              'background-color': s.isGreen ?  '#f56c6c' : '#67c23a',
            }"> {{(s.isGreen ? traffic_gtime2 : traffic_time2)}}</div>

          <div class="color123" :style="{
            'width': (s.isGreen ? traffic_gtime3 : traffic_time3)/60*100 + '%',
            'background-color': s.isGreen ? '#67c23a' : '#f56c6c'
            }"> {{(s.isGreen ? traffic_gtime3 : traffic_time3)}} </div>

          <div class="color123 " :style="{
                'width': (s.isGreen ? traffic_gtime4 : traffic_time4)/60*100 + '%',
                'background-color': s.isGreen ?  '#f56c6c' : '#67c23a'
                }"> {{(s.isGreen ? traffic_gtime4 : traffic_time4)}} </div>

          <div>

          </div>

        </div>
        <div></div>
      </div>

  </div>
</template>
<script>
import {gtime1,gtime2,gtime3,gtime4,rtime1,rtime2,rtime3,rtime4} from '../js/signs.js';
import { ServerBus } from './ServerBus.js';

function sround(val) {
 return String(Math.round(val*100)/100)
}

export default {
  props: ['data'],
  data() {
    return {
      traffic_gtime1: 0,
      traffic_gtime2: 0,
      traffic_gtime3: 0,
      traffic_gtime4: 0,

      traffic_time1: 0,
      traffic_time2: 0,
      traffic_time3: 0,
      traffic_time4: 0,
      ServerBus: ServerBus,
    }
  },
  computed: {
    traffic_time: {
      get() {
        return this.data.time;
      },
      set(v) {
        this.data.time = v;
        this.set_times();
      }
    },
    traffic_green: {
      get() {
        return this.data.time_green;
      },
      set(v) {
        // console.log(v)
        this.data.time_green = v;
        this.set_times();
      }
    },
    traffic_red: {
      get() {
        return this.data.time_red;
      },
      set(v) {
        // console.log(v)
        this.data.time_red = v;
        this.set_times();
      }
    },



  },
  methods: {
    set_times()  {
      this.traffic_gtime1 = gtime1(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_gtime2 = gtime2(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_gtime3 = gtime3(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_gtime4 = gtime4(this.traffic_time,this.traffic_green,this.traffic_red);

      this.traffic_time1 = rtime1(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_time2 = rtime2(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_time3 = rtime3(this.traffic_time,this.traffic_green,this.traffic_red);
      this.traffic_time4 = rtime4(this.traffic_time,this.traffic_green,this.traffic_red);   
      
      ServerBus.$emit('traffic-update', this.data, true);
    },
    sround(val) {
      return String(Math.round(val*100)/100)
    }
  }
}
</script>

<style lang="scss">

.single-traffic-info {

  .second-row {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: row;

      justify-content: start;

      // > span {
      //   width: 90px;
      // }

// .speedcr {
//   // margin-right: 2px;
// }
      .inside {
        width: 100px;
      }

      .text {
        font-weight: bold;
        font-size: 1em;
        line-height: 1em;
        height: 1em;
        margin: 15px;
      }

    }
 .inside-icon {
      width: 100%;
      text-align: right;
      color: #b3d8ff;
    }

    .text {
      // wit
      width: 80%;
    }
    .time {
      width: 40px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: black;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
    }

    .time_red {
      width: 30px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: $blue;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
    }

    .time_green {
      width: 30px;
      // margin-left: auto;
      text-align: right;
      padding-right: 10px;
      color: $purple;
      cursor: pointer;
      // width: 150px;
      :hover {
        color: white;
      }
    }
}

    
</style>
