<template>
  <div id="map" class="map-view">
   
    <div class="loading-screen2" v-show="isLoading_screen">
      <h1>{{isLoading_screenText}}</h1>

<div class="loader2">
  <div class="loader2__bar"></div>
  <div class="loader2__bar"></div>
  <div class="loader2__bar"></div>
  <div class="loader2__bar"></div>
  <div class="loader2__bar"></div>
  <div class="loader2__ball"></div>
</div>

    </div>
      <transition name="fade">
            <div class="popup-dialog" style="" v-show="speed_visible" :style="{
          'left': speed_left + 'px',
          'top': speed_top + 'px'
        }"> <div class="speed">{{speed_text}}</div> </div>
      </transition>

    <transition name="fade">
      <div class="popup-dialog" style="" v-show="pop_visible" :style="{
     'left': pop_left + 'px',
     'top': pop_top + 'px'
   }">
        <div class="light" v-if=" (pop_isGreen ? pop_gtime1 : pop_time1) > 0" :style="{
       
        'background-color': pop_isGreen ? '#67c23a' : '#f56c6c'
        }"> {{ pop_isGreen ? pop_gtime1 : pop_time1 }} s</div>

        <div class="light" :style="{
      
          'background-color': pop_isGreen ?  '#f56c6c' : '#67c23a',
        }"> {{ pop_isGreen ? pop_gtime2 : pop_time2 }} s</div>

        <div class="light" :style="{
        'background-color': pop_isGreen ? '#67c23a' : '#f56c6c'
        }"> {{ pop_isGreen ? pop_gtime3 : pop_time3 }} s </div>

        <div class="light" v-if=" pop_isGreen ? pop_gtime4 : pop_gtime4 > 0" :style="{
        'background-color': pop_isGreen ? '#f56c6c': '#67c23a'
        }"> {{ pop_isGreen ? pop_gtime4 : pop_gtime4 }} s </div>

      </div>
    </transition>



    
    <div id="mapid">
    </div>



  </div>
</template>
<style  lang="scss">
