  left: 10px;
  top: 10px;
  // width: 100%;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  z-index: 5;
}

#mapid {
  width: 100vw;
  height: 100vh;
  border: 1px solid $grey-light;
  margin: 0;
}

.car-label {
  background: #900;
  color: white;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  line-height: 35px !important;
  vertical-align: middle;
