</style>

<script>
import Vue from 'vue'
import 'chart.js';
import 'chartjs-plugin-annotation';
import request from 'request';
import restful, { requestBackend } from 'restful.js';
import convert_text from './import.js';
import {ServerBus} from './ServerBus.js';

