import MessageBox from './MessageBox'
import RightPane from './RightPane'
import NumberInput from './number-input'
import { sign_groups } from '../js/signs.js'

import {dragElement} from '../js/helpers.js'
import { Server } from 'tls';



var ourCustomControl = L.Control.extend({
  options: {
    position: 'topleft' 
    //control position - allowed: 'topleft', 'topright', 'bottomleft', 'bottomright'
  },
});


import {gtime1,gtime2,gtime3,gtime4,rtime1,rtime2,rtime3,rtime4} from '../js/signs.js';


const axios = require('axios');



const copyToClipboard = str => {
  const el = document.createElement('textarea');
  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand('copy');
  document.body.removeChild(el);
};

export default {
  components: {
    'right-pane': RightPane,
    'MessageBox': MessageBox,
    'number-input': NumberInput
  },

  data() {
    return {
      myChart_sim: '',

      genetic_config: {
        "iterations": 100
        , "size": 20
        , "crossover": 0.4
        , "mutation": 0.3
      },
      pop_isGreen: false,
      pop_position: '',
      pop_visible: false,
      pop_left: 0,
      pop_top: 0,
      pop_time: 0,
      pop_red: 60,
      pop_green: 60,

      speed_visible: false,
      speed_left: 0,
      speed_top: 0,
      speed_text: '',

     
      // simulation2_statistics: [], //<- statistics to compare for simulation 2
      isLoading_screen: false,
      isLoading_screenText: 'Loading',
      
      light_group: undefined, //<- traffic_set_single_light


      panelType: '', //'sim', 'cal'
      icon: '',
      car_icon: '',
      map: '',
      zoom:15,
      mapMarkers: [],
      center: [52.2187, 20.9864],
      car: [],
      time: 0,
    
      sign_groups: sign_groups,
    }
  },
  
  computed: {
    pop_time1() {
      return rtime1(this.pop_time, this.pop_green, this.pop_red);
    },
    pop_time2() {
       return rtime2(this.pop_time, this.pop_green, this.pop_red);
    },

    pop_time3() {
       return rtime3(this.pop_time, this.pop_green, this.pop_red);
    },

    pop_time4() {
       return rtime4(this.pop_time, this.pop_green, this.pop_red);
    },

    pop_gtime1() { //green
     return gtime1(this.pop_time, this.pop_green, this.pop_red);
    },

    pop_gtime2() { //red
       return gtime2(this.pop_time, this.pop_green, this.pop_red);
    },

    pop_gtime3() { //green
      return gtime3(this.pop_time, this.pop_green, this.pop_red);
      },

    pop_gtime4() { //green
      return gtime4(this.pop_time, this.pop_green, this.pop_red);
      },    
  
  },

  //  components: { LMap, LTileLayer, LMarker },
  // https://leaflet-extras.github.io/leaflet-providers/preview/ a lot of providers
  mounted() {
    this.icon = L.icon({
        iconUrl: 'https://image.flaticon.com/icons/png/128/803/803227.png',
        iconSize: [30, 25],
        iconAnchor: [0, 0],
        popupAnchor: [0,0],
    });

    this.car_icon = L.icon({
        iconUrl: 'http://icons.iconarchive.com/icons/icons-land/transporter/256/Car-Top-Red-icon.png',
        iconSize: [40, 40],
        iconAnchor: [20, 15],
        popupAnchor: [14,230],
    });

    var map = L.map('mapid', {
    preferCanvas: true
    }).setView(this.center, this.zoom);

//     , {
//     preferCanvas: false
